import { ServiceTask } from "./ServiceTask";
import { BpmnModel } from "./BpmnModel";

export class SplitActivity extends ServiceTask {
  static Clr_Type_ID: string = "bpmn.SplitActivity";
  ClrTypeID: string = SplitActivity.Clr_Type_ID;
  private static MEXTAG: string = 'bpmn.SplitActivity';



  constructor(model: BpmnModel) {
    super(model);
  }

  ResetBeforeLoadFrom() {
    super.ResetBeforeLoadFrom();
  }

  ConvertToJsonObject(): any {
    let obj = super.ConvertToJsonObject();

    return obj;
  }

  LoadFromJsonObject(obj: any) {
    super.LoadFromJsonObject(obj);

  }

  NewInstance(): SplitActivity {
    return new SplitActivity(this.Model);
  }

  clone(): SplitActivity {
    const c = super.clone() as SplitActivity;
    c.Name = this.Name;
    c.NameLanguage = this.NameLanguage && {
      ...this.NameLanguage,
    };
    return c;
  }
}
